export default {
    props:{
        icon: {
            type: Boolean,
            default: false
        },
        tip: {
            type: Boolean,
            default: false
        }
    },
    data(){
        return{
            showConfirm: false,
            flag: false,
        }
    },
    methods: {
        confirm(){
            this.flag = true
            this.$store.dispatch('pushpullFlag', 0)
            this.back()
        },
        back(){
            if(this.tip && !this.flag){
                this.showConfirm = true
            }
            else{
                this.$store.dispatch('pushcMed', null)
                const now = location.href.split('?')[0]
                const start = this.$store.state.startUrl
                if(now === start){
                    const data = '123'
                    this.$bridge.callhandler('Back', data)
                }
                else{
                    this.$router.back()
                }
            }
        }
    }
}



